.banner{
    
    background-size: cover;
    height: 820px;
    color: aliceblue;
}

.content{
    padding-top: 344px;
    height: 190px;
    padding-left: 100px;
}

.tittel{
    font-size: 3rem ;
    font-weight: 800;
    padding-bottom: .3rem;
}

.button{
    color: aliceblue;
    outline: none;
    border: none;
    font-weight: 700;
    border-radius: 5px;
    padding-left: 2rem;
    padding-right: 2rem;
    padding-top: .5rem;
    padding-bottom: .5rem;
    background-color: rgb(51, 51, 51,.5);
    cursor: pointer;
    margin-right: 1rem;
}
button:hover{
    color: black;
    background-color: #e6e6e6;
}

.des{
    width: 45rem;
    line-height: 1.3;
    padding-top: 1rem;
    font-size: 1rem;
    height: 80px;
    max-width: 360px;
}

.fade{
    height: 500.4px;
    background-image: linear-gradient(180deg,transparent,rgba(37,37,37,.51),#111);

}

.posters{
    display: flex;
    padding: 20px;
    overflow-x: scroll;
    overflow-y: hidden;
}